import React from 'react';

import { Link } from 'react-router-dom';

const CountryDropdown = React.lazy(() => import("./countrydropdown/CountryDropdown"));

const Navbar = () => {

    return (
        <div className='d-flex justify-content-between align-items-center py-1 px-md-5 px-2' style={{ background: "#EBEBEB" }}>

            <nav className="navbar navbar-expand-lg">

                <div className="container-fluid">

                    <div className="" id="navbarSupportedContent">
                        <ul className="navbar-nav me-auto mb-2 mb-lg-0">

                            <li className="nav-item">
                                <div className="dropdown">
                                    <button className="btn" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Outdoor Blinds
                                    </button>
                                    <ul className="dropdown-menu">
                                        <li><Link className="dropdown-item" to="/">Action</Link></li>
                                        <li><Link className="dropdown-item" to="/">Another action</Link></li>
                                        <li><Link className="dropdown-item" to="/">Something else here</Link></li>
                                    </ul>
                                </div>
                            </li>

                            <li className="nav-item">
                                <div className="dropdown">
                                    <button className="btn" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Interior Blinds
                                    </button>
                                    <ul className="dropdown-menu">
                                        <li><Link className="dropdown-item" to="/">Action</Link></li>
                                        <li><Link className="dropdown-item" to="/">Another action</Link></li>
                                        <li><Link className="dropdown-item" to="/">Something else here</Link></li>
                                    </ul>
                                </div>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">FAQ</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Blog</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Inspiration Gallery</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Support</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Trade Support</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Find Reatailer</Link>
                            </li>

                            <li className="nav-item">
                                <Link className="nav-link active" aria-current="page" to="/">Contact</Link>
                            </li>

                        </ul>

                    </div>
                </div>
            </nav>

            <div className='d-none d-md-block'>
                <CountryDropdown />
            </div>

        </div>

    )

}

export default Navbar